import React from 'react';
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from 'chart.js';
import { Radar } from 'react-chartjs-2';

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
);

interface QualityChartProps {
  data: {
    [key: string]: {
      score: number;
      maxScore: number;
      percentage: number;
    };
  };
}

const QualityChart: React.FC<QualityChartProps> = ({ data }) => {
  // Filter out categories with no max score
  const validCategories = Object.entries(data).filter(([_, scores]) => scores.maxScore > 0);
  
  if (validCategories.length === 0) {
    return (
      <div className="text-center text-muted py-4">
        <p>No quality data available for chart</p>
      </div>
    );
  }

  const chartData = {
    labels: validCategories.map(([category]) => 
      category.charAt(0).toUpperCase() + category.slice(1)
    ),
    datasets: [
      {
        label: 'Quality Score (%)',
        data: validCategories.map(([_, scores]) => scores.percentage),
        backgroundColor: 'rgba(13, 110, 253, 0.2)',
        borderColor: 'rgba(13, 110, 253, 1)',
        borderWidth: 2,
        pointBackgroundColor: 'rgba(13, 110, 253, 1)',
        pointBorderColor: '#fff',
        pointHoverBackgroundColor: '#fff',
        pointHoverBorderColor: 'rgba(13, 110, 253, 1)',
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top' as const,
      },
      tooltip: {
        callbacks: {
          label: function(context: any) {
            const categoryIndex = context.dataIndex;
            const [category, scores] = validCategories[categoryIndex];
            return `${category}: ${scores.score}/${scores.maxScore} (${scores.percentage.toFixed(1)}%)`;
          }
        }
      }
    },
    scales: {
      r: {
        beginAtZero: true,
        max: 100,
        ticks: {
          stepSize: 20,
          callback: function(value: any) {
            return value + '%';
          }
        },
        grid: {
          color: 'rgba(0, 0, 0, 0.1)',
        },
        angleLines: {
          color: 'rgba(0, 0, 0, 0.1)',
        }
      },
    },
  };

  return (
    <div style={{ height: '400px', position: 'relative' }}>
      <Radar data={chartData} options={options} />
    </div>
  );
};

export default QualityChart;
