import { Parser as N3Parser, Writer as N3Writer, Store as N3Store } from 'n3';
import { RdfXmlParser } from 'rdfxml-streaming-parser';
import { RDFFormat, ValidationInput } from '../types';

export class RDFService {
  private static instance: RDFService;
  
  public static getInstance(): RDFService {
    if (!RDFService.instance) {
      RDFService.instance = new RDFService();
    }
    return RDFService.instance;
  }

  /**
   * Detect RDF format from content
   */
  public detectFormat(content: string): RDFFormat {
    // Trim whitespace and check for XML declaration or RDF/XML root elements
    const trimmed = content.trim();
    
    if (trimmed.startsWith('<?xml') || 
        trimmed.includes('<rdf:RDF') || 
        trimmed.includes('<RDF') ||
        trimmed.includes('xmlns:rdf=')) {
      return 'rdfxml';
    }
    
    // Check for JSON-LD
    if (trimmed.startsWith('{') || trimmed.startsWith('[')) {
      try {
        const parsed = JSON.parse(trimmed);
        if (parsed['@context'] || parsed['@graph'] || parsed['@id']) {
          return 'jsonld';
        }
      } catch (e) {
        // Not valid JSON, continue checking
      }
    }
    
    // Check for N-Triples (simple format)
    if (trimmed.includes('<') && trimmed.includes('>') && trimmed.includes(' .')) {
      const lines = trimmed.split('\n');
      const ntriplesPattern = /^<[^>]+>\s+<[^>]+>\s+.*\s+\.$/;
      if (lines.some(line => ntriplesPattern.test(line.trim()))) {
        return 'ntriples';
      }
    }
    
    // Default to Turtle
    return 'turtle';
  }

  /**
   * Convert RDF/XML to Turtle using rdfxml-streaming-parser
   */
  public async convertRdfXmlToTurtle(rdfXmlContent: string, baseIRI: string = 'http://example.org/'): Promise<string> {
    try {
      console.log('🔄 Converting RDF/XML to Turtle using rdfxml-streaming-parser...');
      
      const parser = new RdfXmlParser({ baseIRI });
      const store = new N3Store();
      
      return new Promise((resolve, reject) => {
        parser.on('data', (quad) => {
          store.addQuad(quad);
        });
        
        parser.on('error', (error) => {
          console.error('❌ RDF/XML Parser error:', error);
          reject(new Error(`RDF/XML parsing failed: ${error.message}`));
        });
        
        parser.on('end', () => {
          try {
            const writer = new N3Writer({
              prefixes: {
                rdf: 'http://www.w3.org/1999/02/22-rdf-syntax-ns#',
                rdfs: 'http://www.w3.org/2000/01/rdf-schema#',
                dcat: 'http://www.w3.org/ns/dcat#',
                dcterms: 'http://purl.org/dc/terms/',
                foaf: 'http://xmlns.com/foaf/0.1/',
                vcard: 'http://www.w3.org/2006/vcard/ns#',
                adms: 'http://www.w3.org/ns/adms#'
              }
            });

            const quads = store.getQuads();
            console.log(`✅ Parsed ${quads.length} quads from RDF/XML`);
            
            writer.addQuads(quads);
            writer.end((error, result) => {
              if (error) {
                reject(error);
              } else {
                console.log('✅ RDF/XML successfully converted to Turtle');
                resolve(result);
              }
            });
          } catch (conversionError) {
            reject(conversionError);
          }
        });

        // Start parsing
        parser.write(rdfXmlContent);
        parser.end();
      });
      
    } catch (error) {
      console.error('❌ Failed to convert RDF/XML to Turtle:', error);
      throw new Error(`RDF/XML conversion failed: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  /**
   * Fetch content from URL
   */
  public async fetchFromUrl(url: string): Promise<string> {
    try {
      console.log(`🌐 Fetching content from URL: ${url}`);
      const response = await fetch(url);
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }
      
      const content = await response.text();
      console.log(`✅ Successfully fetched ${content.length} characters from URL`);
      return content;
    } catch (error) {
      console.error('❌ Failed to fetch from URL:', error);
      throw new Error(`Failed to fetch from URL: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  /**
   * Normalize RDF content to Turtle format
   */
  public async normalizeToTurtle(input: ValidationInput): Promise<string> {
    let content = input.content;
    let format = input.format;

    // If input is from URL, fetch the content first
    if (input.source === 'url') {
      content = await this.fetchFromUrl(input.content);
    }

    // Auto-detect format if needed
    if (format === 'auto') {
      format = this.detectFormat(content);
      console.log(`🔍 Auto-detected format: ${format}`);
    }

    // Convert RDF/XML to Turtle if needed
    if (format === 'rdfxml') {
      content = await this.convertRdfXmlToTurtle(content);
      format = 'turtle';
    }

    // For other formats, we'll assume they can be processed directly
    // In a full implementation, you'd add JSON-LD and N-Triples conversion
    
    return content;
  }

  /**
   * Parse RDF content and return quad count
   */
  public async parseAndCount(content: string, format: RDFFormat = 'turtle'): Promise<number> {
    try {
      const parser = new N3Parser({ format: format === 'turtle' ? 'text/turtle' : format });
      const store = new N3Store();
      
      return new Promise((resolve, reject) => {
        parser.parse(content, (error, quad, prefixes) => {
          if (error) {
            reject(error);
          } else if (quad) {
            store.addQuad(quad);
          } else {
            // End of parsing
            resolve(store.size);
          }
        });
      });
    } catch (error) {
      console.error('Error parsing RDF content:', error);
      return 0;
    }
  }

  /**
   * Validate RDF syntax
   */
  public async validateSyntax(content: string, format: RDFFormat): Promise<{ isValid: boolean; errors: string[] }> {
    try {
      await this.parseAndCount(content, format);
      return { isValid: true, errors: [] };
    } catch (error) {
      return { 
        isValid: false, 
        errors: [error instanceof Error ? error.message : 'Unknown parsing error'] 
      };
    }
  }
}

export default RDFService.getInstance();
